'use client';

import { useEffect, useState } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import { API_URL } from "../api/apiURL";

// Fix Highcharts Marker (Smooth)
if (typeof Highcharts === "object") {
  require("highcharts/modules/exporting")(Highcharts);
}

/* -------------------------------
 CONSTANT WEEK STRUCTURE
-------------------------------- */
const WEEK_ORDER = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
const WEEK_LABELS = { Sun: "Su", Mon: "Mo", Tue: "Tu", Wed: "We", Thu: "Th", Fri: "Fr", Sat: "Sa" };

/* -------------------------------
 MATCHING SCREENSHOT SHAPE
-------------------------------- */
const SS_YELLOW = [24, 22, 17, 18, 22, 26, 29]; // M
const SS_GREEN = [10, 15, 13, 14, 15, 20, 25]; // M

export default function DashboardFinal() {

  const [trendData, setTrend] = useState([]);
  const [funnelData, setFunnel] = useState([]);

  /* -------------------------------
       FETCH TREND API
  -------------------------------- */
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-signup-trends`);
        const json = await res.json();
        if (!json.success) return;

        const bucket = {};
        json.data.forEach((d) => {
          bucket[d.date] = Number(d.newUsers || 0);
        });

        setTrend(WEEK_ORDER.map((day) => bucket[day] || 0));
      } catch (err) {
        console.log(err);
      }
    })();
  }, []);

  /* -------------------------------
       FETCH FUNNEL API
  -------------------------------- */
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(`${API_URL}/admin/search-to-match`);
        const json = await res.json();
        if (json.success) setFunnel(json.data);
      } catch (err) {
        console.log(err);
      }
    })();
  }, []);

  /* -------------------------------
  FINAL CHART VALUES BASED ON API
  -------------------------------- */
  const yellowData = trendData.map((v, i) => v * SS_YELLOW[i] * 1_000_000);
  const greenData = trendData.map((v, i) => v * SS_GREEN[i] * 1_000_000);

  /* -------------------------------
       HIGHCHART CONFIG
  -------------------------------- */
  const chartOptions = {
    chart: {
      type: "line",
      height: 280,
      backgroundColor: "#fff",
      spacing: [15, 10, 10, 10],
    },

    title: { text: "" },

    xAxis: {
      categories: WEEK_ORDER.map((d) => WEEK_LABELS[d]),
      tickLength: 0,
      labels: {
        style: { fontSize: "15px", fontWeight: 600, color: "#4b5563" },
        y: 8,
      },
    },

    yAxis: {
      title: null,
      min: 0,
      max: 30_000_000,
      tickPositions: [0, 5e6, 10e6, 15e6, 20e6, 25e6, 30e6],
      labels: {
        formatter() {
          return this.value / 1_000_000 + " M";
        },
        style: { fontSize: "14px", fontWeight: 500, color: "#6b7280" },
      },
      gridLineDashStyle: "ShortDash",
    },

    tooltip: {
      shared: true,
      borderRadius: 10,
      borderWidth: 1,
      backgroundColor: "#fff",
      style: { fontSize: "15px" },
      formatter() {
        return `
          <b>${this.x}</b><br/>
          ðŸŸ¡ New Users: <b>${(this.points[0].y / 1_000_000).toFixed(1)}M</b><br/>
          ðŸŸ¢ Returning: <b>${(this.points[1].y / 1_000_000).toFixed(1)}M</b>
        `;
      },
    },

    plotOptions: {
      line: {
        lineWidth: 3,
        marker: {
          radius: 6,
          lineWidth: 3,
          fillColor: "#fff",
        },
      },
    },

    series: [
      {
        name: "New Registered Users",
        data: yellowData,
        color: "#facc15",
        marker: { lineColor: "#facc15" },
      },
      {
        name: "Returning Users",
        data: greenData,
        color: "#16a34a",
        marker: { lineColor: "#16a34a" },
      },
    ],
  };

  return (
    <div className="mt-24 px-6 flex flex-col md:flex-row gap-10 w-full">

      {/* LEFT PANEL */}
      <div className="bg-white rounded-[32px] shadow-xl border border-gray-300 p-10 w-full md:w-1/2">
        <h2 className="text-3xl font-bold mb-10">Search To Match</h2>

        <div className="space-y-10">
          {funnelData.map((item, index) => {
            const max = funnelData[0]?.value || 1;
            const pct = (item.value / max) * 100;

            return (
              <div key={index} className="flex items-center justify-between">
                <div className="w-[240px] text-[20px] font-semibold text-gray-800">{item.stage}</div>

                <div className="flex-1 mx-4">
                  <div className="w-full h-[30px] rounded-full bg-gray-200 overflow-hidden shadow-inner">
                    <div
                      className="h-full"
                      style={{
                        width: `${pct}%`,
                        background: "linear-gradient(90deg,#3A47FF,#001BFF)",
                        borderRadius: 20,
                      }}
                    ></div>
                  </div>
                </div>

                <div className="w-[120px] text-right text-[20px] font-semibold">{item.value}</div>
              </div>
            );
          })}
        </div>
      </div>

      {/* RIGHT PANEL */}
      <div className="bg-white rounded-[32px] shadow-xl border border-gray-300 p-10 w-full md:w-1/2">

        {/* Header + Legend */}
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-[26px] font-bold">Running User Trend</h2>

          <div className="flex items-center gap-10 text-gray-700 text-[16px] font-semibold">
            <div className="flex items-center gap-2">
              <span style={{ width: 22, height: 4, background: "#facc15", borderRadius: 4 }} />
              <span style={{ width: 14, height: 14, borderRadius: "50%", border: "3px solid #facc15", background: "#fff" }} />
              New Users
            </div>

            <div className="flex items-center gap-2">
              <span style={{ width: 22, height: 4, background: "#16a34a", borderRadius: 4 }} />
              <span style={{ width: 14, height: 14, borderRadius: "50%", border: "3px solid #16a34a", background: "#fff" }} />
              Returning
            </div>
          </div>
        </div>

        <HighchartsReact highcharts={Highcharts} options={chartOptions} />
      </div>
    </div>
  );
}
